import { createRouter, createWebHistory } from 'vue-router';
import HomePage from '@/views/HomePage.vue';  // Ensure file name matches
import TransactionsPage from '@/views/TransactionsPage.vue';  // Ensure file name matches

const routes = [
  {
    path: '/',
    name: 'HomePage',
    component: HomePage
  },
  {
    path: '/transactions',
    name: 'TransactionsPage',
    component: TransactionsPage
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes
});

export default router;
